import React, { useState, useEffect, createRef } from 'react';
import { CircularProgress, Grid, Typography, Box, CardActions } from '@material-ui/core';
// import { FormGroup, FormControlLabel, Checkbox, FormControl } from '@material-ui/core';
import { ToggleButtonGroup, ToggleButton } from '@material-ui/lab';




import PlaceDetails from '../PlaceDetails/PlaceDetails';
import useStyles from './styles.js';

const List = ({ places, timeFilter, setTimeFilter, childClicked, isLoading, coords }) => {
  const [elRefs, setElRefs] = useState([]);
  const classes = useStyles();

  const handleTimeFilter = (event, newTimeFilter) => {
    setTimeFilter(newTimeFilter);
  };

  useEffect(() => {
    setElRefs((refs) => Array(places.length).fill().map((_, i) => refs[i] || createRef()));
  }, [places]);

  return (
    <div className={classes.container}>
      {isLoading ? (
        <>
          <Typography variant="h4">Loading Happiness ðŸ˜€</Typography>
          <div className={classes.loading}>
            <CircularProgress size="5rem" />
          </div>
        </>
      ) : (
        <>
          <Typography variant="h4">Found {places.length} happening {timeFilter === 'now' ? 'now' : 'today'} {places.length === 0 && ':('}</Typography>
          <CardActions style={{justifyContent: 'center'}}>

          <ToggleButtonGroup
            value={timeFilter}
            exclusive
            onChange={handleTimeFilter}
          >
            <ToggleButton value="now">
              Now
            </ToggleButton>
            <ToggleButton value="today">
              Today
            </ToggleButton>
          </ToggleButtonGroup>
          <Box sx={{ m: 0.5 }} />
          </CardActions>
          {/* <FormControl className={classes.formControl}>
            <FormGroup row>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={happyNow}
                    onChange={(event) => setHappyNow(event.target.checked)}
                    name="happyNow"
                    color="primary"
                  />
                }
                label="Now"
              />
                            <FormControlLabel
                control={
                  <Checkbox
                    checked={happyToday}
                    onChange={(event) => setHappyToday(event.target.checked)}
                    name="happyToday"
                    color="primary"
                  />
                }
                label="Today"
              />
            </FormGroup>
          </FormControl> */}
          <br></br>
          <Grid container spacing={3} className={classes.list}>
            {places?.map((place, i) => (
              <Grid ref={elRefs[i]} key={i} item xs={12}>
                <PlaceDetails selected={Number(childClicked) === i} refProp={elRefs[i]} place={place} coords={coords} />
              </Grid>
            ))}
          </Grid>
        </>
      )
      }
    </div >
  );
};

export default List;
